import React, { Component } from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';

import { email, socialMedia } from '../config';
import {
  IconGithub,
  IconLinkedin,
  IconCodeSandbox,
  IconDribbble,
  IconTwitter,
  IconBehance,
} from './icons';

import styled from 'styled-components';
import { theme, media, A, Ul } from '../styles';

const EmailContainer = styled.div`
  width: 40px;
  position: fixed;
  bottom: 0;
  right: 40px;
  color: ${theme.colors.darkblueMedium};
  ${media.desktop`right: 25px;`};
  ${media.tablet`display: none;`};
  div {
    width: 100%;
    margin: 0 auto;
  }
`;
const EmailLinkWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  &:after {
    content: '';
    display: block;
    width: 1px;
    height: 60px;
    margin: 0 auto;
    background-color: ${theme.colors.darkblueMedium};
  }
`;
const EmailLink = styled(A)`
  font-family: ${theme.fonts.RBNo31};
  font-size: ${theme.fontSizes.xsmall};

  letter-spacing: 0.5px;
  writing-mode: vertical-rl;
  margin: 10px auto;
  padding: 6px;
`;

const SocialContainer = styled.div`
  width: 40px;
  position: fixed;
  bottom: 0;
  left: 40px;
  color: ${theme.colors.darkblueMedium};
  ${media.desktop`left: 25px;`};
  ${media.tablet`display: none;`};
`;
const SocialItemList = styled(Ul)`
  display: flex;
  flex-direction: column;
  align-items: center;
`;
const SocialItem = styled.li`
  &:last-of-type {
    margin-bottom: 0px;
  }
`;
const SocialLink = styled(A)`
  padding: 10px;
  svg {
    width: 18px;
    height: 18px;
  }
`;

class Email extends Component {
  state = {
    isMounted: false,
  };

  componentDidMount() {
    setTimeout(() => this.setState({ isMounted: true }), 2000);
  }

  render() {
    const { isMounted } = this.state;

    return (
      <EmailContainer>
        <TransitionGroup>
          {isMounted && (
            <CSSTransition timeout={3000} classNames="fade">
              <EmailLinkWrapper>
                <SocialItemList>
                  {socialMedia &&
                    socialMedia.map(({ url, name }, i) => (
                      <SocialItem key={i}>
                        <SocialLink
                          href={url}
                          target="_blank"
                          rel="nofollow noopener noreferrer"
                          aria-label={name}>
                          {name === 'Dribbble' ? (
                            <IconDribbble />
                          ) : name === 'Behance' ? (
                            <IconBehance />
                          ) : name === 'CodeSandbox' ? (
                            <IconCodeSandbox />
                          ) : name === 'Linkedin' ? (
                            <IconLinkedin />
                          ) : name === 'Twitter' ? (
                            <IconTwitter />
                          ) : (
                            <IconGithub />
                          )}
                        </SocialLink>
                      </SocialItem>
                    ))}
                </SocialItemList>

                <EmailLink href={`mailto:${email}`}>{email}</EmailLink>
              </EmailLinkWrapper>
            </CSSTransition>
          )}
        </TransitionGroup>
      </EmailContainer>
    );
  }
}

export default Email;
