import React, { Component } from 'react';
import Helmet from 'react-helmet';
import { CSSTransition, TransitionGroup } from 'react-transition-group';

import AnchorLink from 'react-anchor-link-smooth-scroll';
import { Link } from 'gatsby';
import { throttle } from '../utils';

import { headerHeight } from '../config';
import resume from '../images/willyan_geraldo.pdf';

import styled from 'styled-components';
import { theme, mixins, media, Nav, Ol, A } from '../styles';

const HeaderContainer = styled.header`
  ${mixins.flexBetween};
  position: relative;
  top: 0;
  padding: 0px 50px;
  background-color: transparent;
  transition: ${theme.transition};
  z-index: 11;
  filter: none !important;
  pointer-events: auto !important;
  user-select: auto !important;
  width: 100%;
  height: ${props =>
    props.scrollDirection === 'none' ? theme.headerHeight : theme.headerScrollHeight};
  box-shadow: ${props =>
    props.scrollDirection === 'up' ? `0 2px 4px ${theme.colors.shadowNavy}` : 'none'};
  transform: translateY(
    ${props => (props.scrollDirection === 'down' ? `-${theme.headerScrollHeight}` : '0px')}
  );
  ${media.desktop`padding: 0 40px;`};
  ${media.tablet`padding: 0 25px;`};
`;
const Navbar = styled(Nav)`
  font-family: ${theme.fonts.SFMono};
  width: 100%;
  counter-reset: item 0;
  position: relative;
  z-index: 12;
`;

const NavLinks = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-end;
`;

const ResumeButton = styled.div``;
const ResumeLink = styled(A)`
  ${mixins.smallButton};
  margin-left: 10px;
  font-size: ${theme.fontSizes.smallish};
`;

const DELTA = 5;

class Header extends Component {
  state = {
    isMounted: false,
    scrollDirection: 'none',
  };

  componentDidMount() {
    setTimeout(() => this.setState({ isMounted: true }), 800);
  }

  render() {
    const { scrollDirection, isMounted } = this.state;

    return (
      <TransitionGroup>
        {isMounted && (
          <CSSTransition timeout={2000} classNames="fade">
            <HeaderContainer scrollDirection={scrollDirection}>
              <Navbar>
                <NavLinks>
                  <ResumeButton style={{ transitionDelay: `600ms` }}>
                    <ResumeLink href={resume} target="_blank" rel="nofollow noopener noreferrer">
                      Resume
                    </ResumeLink>
                  </ResumeButton>
                </NavLinks>
              </Navbar>
            </HeaderContainer>
          </CSSTransition>
        )}
      </TransitionGroup>
    );
  }
}

export default Header;
