import React, { PureComponent } from 'react';

import { socialMedia } from '../config';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import {
  IconGithub,
  IconLinkedin,
  IconCodeSandbox,
  IconDribbble,
  IconTwitter,
  IconBehance,
} from './icons';

import styled from 'styled-components';
import { theme, mixins, media, A, P, Ul } from '../styles';

const FooterContainer = styled.footer`
  position: relative;
  bottom: 0;
  margin-bottom: 0;
  ${media.tablet`
  position: absolute;
  `};
  ${media.thone`
  position: relative;
  margin-top: 30px;
  `};
  width: 100%;
  flex-direction: column;
  ${mixins.flexCenter};

  padding: 10px;
  background-color: transparent;
  color: ${theme.colors.slate};
  text-align: center;
  height: 40px;
`;
const SocialContainer = styled.div`
  color: ${theme.colors.socialLight};
  width: 100%;
  max-width: 80%;
  margin: 0 auto 10px;
  display: none;
  ${media.tablet`display: block; margin-bottom: 50px;`};
  ${media.thone`display: block; margin-bottom: 10px;`};
`;
const SocialItemList = styled(Ul)`
  ${mixins.flexBetween};
`;
const SocialItem = styled.li``;
const SocialLink = styled(A)`
  padding: 10px;
  svg {
    width: 20px;
    height: 20px;
  }
  ${media.tablet`
  padding: 20px;
  svg {
    width: 26px;
    height: 26px;
  }`};
  ${media.thone`
  padding: 10px;
  svg {
    width: 20px;
    height: 20px;
  }`};
`;
const Copy = styled(P)`
  margin: 5px 0 3px;
`;

class Footer extends React.Component {
  state = {
    isShow: false,
  };

  componentDidMount() {
    setTimeout(() => this.setState({ isShow: true }), 2000);
  }

  render() {
    const { isShow } = this.state;

    return (
      <TransitionGroup>
        {isShow && (
          <CSSTransition timeout={2000} classNames="fade">
            <FooterContainer>
              <SocialContainer>
                <SocialItemList>
                  {socialMedia &&
                    socialMedia.map(({ name, url }, i) => (
                      <SocialItem key={i}>
                        <SocialLink
                          href={url}
                          target="_blank"
                          rel="nofollow noopener noreferrer"
                          aria-label={name}>
                          {name === 'Behance' ? (
                            <IconBehance />
                          ) : name === 'Linkedin' ? (
                            <IconLinkedin />
                          ) : name === 'CodeSandbox' ? (
                            <IconCodeSandbox />
                          ) : name === 'Dribbble' ? (
                            <IconDribbble />
                          ) : name === 'Twitter' ? (
                            <IconTwitter />
                          ) : (
                            <IconGithub />
                          )}
                        </SocialLink>
                      </SocialItem>
                    ))}
                </SocialItemList>
              </SocialContainer>
            </FooterContainer>
          </CSSTransition>
        )}
      </TransitionGroup>
    );
  }
}

export default Footer;
